



































export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			autoplay: false,
			duration: 500,
			jumpover: '跳过',
			experience: '立即体验',
			height:''
		}
	},
	onShow() {
		this.height = uni.getSystemInfoSync().screenHeight;
	},
	methods: {
		launchFlag: function() {
			/**
			 * 向本地存储中设置launchFlag的值，即启动标识；
			 */
			uni.setStorage({
				key: 'launchFlag',
				data: true,
			});
			uni.switchTab({
				url: '/pages/index/index'
			});

		}
	}
}
